.App {
  background-color: black;
  height: 100vh;
  text-align: center;
  padding-top: 1rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.link {
  width: 10rem;
  height: 2rem;
  margin: 0.5rem;
  padding: 0.5rem;
  background-color: wheat;
  color: black;
  border-radius: 5px;
  border: 1px solid black;
  text-decoration: none;
}
